body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Ubuntu";
    background: gainsboro;
}

* {
    box-sizing: border-box;
}

.Layout {
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-flow: column;   
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: gainsboro;;
}

::-webkit-scrollbar-thumb {
    background: royalblue;
    border-radius: 0;
}